<template>
  <div class="home">
    <el-container>
      <el-aside width="200px">Aside</el-aside>
      <el-container>
        <el-header>
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect">
            <el-menu-item index="1">Centre de traitement</el-menu-item>
            <el-submenu index="2">
              <template slot="title">Lieu de travail</template>
              <el-menu-item index="2-1">item un</el-menu-item>
              <el-menu-item index="2-2">item deux</el-menu-item>
              <el-menu-item index="2-3">item trois</el-menu-item>
              <el-submenu index="2-4">
                <template slot="title">item quatre</template>
                <el-menu-item index="2-4-1">item un</el-menu-item>
                <el-menu-item index="2-4-2">item deux</el-menu-item>
                <el-menu-item index="2-4-3">item trois</el-menu-item>
              </el-submenu>
            </el-submenu>
            <el-menu-item index="3" disabled>Infos</el-menu-item>
            <el-menu-item index="4"><a href="https://www.ele.me" target="_blank">Commandes</a></el-menu-item>
          </el-menu>
        </el-header>
        <el-main>
          <button
            id="checkout-button-plan_DfGHNBHbdsY2F7"
            role="link"
          >
            Checkout
          </button>

          <el-button @click="build">
            Build
          </el-button>

          <el-table
            :data="logs"
            class="log-table"
            row-class-name="row-log-table"
          >
            <el-table-column
              prop="scope"
              label="Scope"
              width="180">
            </el-table-column>
            <el-table-column
              prop="message"
              label="Message"
            >
              <template slot-scope="scope">
                <div v-html="scope.row.message"></div>
              </template>
            </el-table-column>
          </el-table>

          <div id="error-message"></div>
        </el-main>
        <el-footer>Footer</el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>

import io from 'socket.io-client';

const AU = require('ansi_up');

// eslint-disable-next-line
  const ansiUp = new AU.default();

const socket = io('http://localhost:56342');

export default {
  name: 'home',
  data() {
    return {
      logs: [],
      activeIndex: '1',
      activeIndex2: '1',
    };
  },
  methods: {
    build() {
      socket.emit('build');
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
  },
  mounted() {
    const stripe = Stripe('pk_test_KZNTxg8s6BYdJYpL4EfxTHPR');

    const checkoutButton = document.getElementById('checkout-button-plan_DfGHNBHbdsY2F7');
    checkoutButton.addEventListener('click', () => {
      // When the customer clicks on the button, redirect
      // them to Checkout.
      stripe.redirectToCheckout({
        items: [{
          plan: 'plan_DfGHNBHbdsY2F7',
          quantity: 1,
        }],

        // Note that it is not guaranteed your customers will be redirected to this
        // URL *100%* of the time, it's possible that they could e.g. close the
        // tab between form submission and the redirect.
        successUrl: 'https://efc.armaldio.xyz/success',
        cancelUrl: 'https://efc.armaldio.xyz/canceled',
        customerEmail: 'customer@example.com',
      }).then((result) => {
        if (result.error) {
          // If `redirectToCheckout` fails due to a browser or network
          // error, display the localized error message to your customer.
          const displayError = document.getElementById('error-message');
          displayError.textContent = result.error.message;
        }
      });
    });

    socket.on('log', (message) => {
      const html = ansiUp.ansi_to_html(message).trim();
      if (html === '') return;
      this.logs.push({
        scope: '',
        message: html,
      });
    });
  },
};
</script>

